@import 'css-essential';

#p1 {
	@extend %full-page;
	@include background-image(url("../assets/firstLogo.png"));
	@extend %parallax;
	background-color: #3489aae8;
}

#welcome {
	font-size: 7vw;
	color: rgb(26, 9, 9);
	width: 100%;
	text-align: center;
	position: absolute;
	top: 20%;
	animation: welcomeanum 10s infinite;
}

@mixin welcomeanumoff {
	opacity: 0;
	font-size: 0.0001;
}
@mixin welcomeanumhalf {
	opacity: 0;
	font-size: 7vw;
}

@keyframes welcomeanum {
	0% {
		@include welcomeanumoff;
	}
	20% {
		@include welcomeanumoff;
	}
	40% {
		@include welcomeanumhalf;
	}
	60% {
		opacity: 1;
		font-size: 7vw;
	}
	80% {
		@include welcomeanumhalf;
	}
	100% {
		@include welcomeanumoff;
	}
}

$b612monospace : 'B612 Mono', monospace;

#hal {
	width: 100%;
	text-align: center;
	position: absolute;
	top: 35%;
	@include text-font-color-and-size($b612monospace,black,8vw);

}

#hal span {
	animation: halspananum 10s infinite;
}

@mixin halspananumoff {
	opacity: 0;
	font-size: 0.0001;
}

@mixin halspananumofffok {
	opacity: 0;
	font-size: 8vw;
}

@keyframes halspananum {
	0% {
		@include halspananumoff;
	}
	20% {
		@include halspananumoff;
	}
	40% {
		@include halspananumofffok;
	}
	60% {
		opacity: 1;
		font-size: 8vw;
	}
	80% {
		@include halspananumofffok;
	}
	100% {
		@include halspananumoff;
	}
}



#p2 {
	@extend %full-page;
	@include background-image(linear-gradient(to right, #343434, black));
	font-size: 3.5vw;
	text-align: center;
	color: white;
	padding-right: 10%;
	padding-left: 10%;
	padding-top: 10%;
}