body {
    margin: 0%;
    overflow-x: hidden;
}

%parallax {
	background-size: cover;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-position: center;
}

%full-page {
	height: 100%;
	width: 100%;
}

@mixin background-image($img) {
	background-image:$img;
}

@mixin text-font-color-and-size($font,$color,$size) {
    font-family: $font;
    color: $color;
    font-size: $size;
}